
import CustomDropdown from './lib/CustomDropdown.vue'
import DarkModeToggle from './lib/DarkModeToggle.vue'
export default {
  components: {
    CustomDropdown,
    DarkModeToggle
  },
  props: {
    currentLocale: {
      type: String,
      required: true,
    },
    file: {
      type: String,
      required: true,
    },
    path: {
      type: String,
      required: true,
    },
    files: {
      type: Object,
      required: true,
    },
    doc: {
      type: Object,
      required: true
    },
    isDarkMode: {
      type: Boolean,
      required: true,
    },
    locales: {
      type: Object,
      required: true,
    },
  },
  computed: {

  },
  methods: {
    changeDoc(option) {
      return '/' + this.path + '/' + option + '/' + this.currentLocale + '/get/info';
    },
    changeLocale(option) {
      return '/' + this.path + '/' + this.file + '/' + option + '/get/info';
    },
    downloadYaml() {
      const json = JSON.stringify(this.doc);
      const blob = new Blob([json], { type: 'application/json' });
      const url = URL.createObjectURL(blob);

      const link = document.createElement('a');
      link.href = url;
      link.download = this.file + '.yaml';
      document.body.appendChild(link);

      link.click();

      // Очистить ссылку и объект URL после скачивания файла
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    }
  },
};
